'use client';

import React, { useState, useEffect } from 'react';
import { CalendarIcon, TrashIcon, PlusIcon } from '@heroicons/react/24/outline';

// Types
interface Supplier {
  id: number;
  name: string;
  address: string;
  gstIn: string;
}

interface PurchaseItem {
  id: number;
  itemCode: string;
  medicineName: string;
  batchNo: string;
  expiryDate: string;
  qty: number;
  freeQty: number;
  purchaseRate: number;
  sellingRate: number;
  gstPercent: number;
  amount: number;
  hsnCode: string;
}

interface PurchaseBill {
  purchaseId: number;
  date: string;
  supplierId: number;
  supplierName: string;
  supplierAddress: string;
  gstIn: string;
  invoiceNo: string;
  invoiceDate: string;
  paymentType: string;
  items: PurchaseItem[];
  grossTotal: number;
  gstTotal: number;
  roundOff: number;
  discount: number;
  cgst: number;
  sgst: number;
  igst: number;
  cessAmount: number;
  finalTotal: number;
}

const PurchaseBillPage: React.FC = () => {
  // State management
  const [purchaseBill, setPurchaseBill] = useState<PurchaseBill>({
    purchaseId: 1,
    date: new Date().toISOString().split('T')[0],
    supplierId: 0,
    supplierName: '',
    supplierAddress: '',
    gstIn: '',
    invoiceNo: '',
    invoiceDate: new Date().toISOString().split('T')[0],
    paymentType: 'Credit',
    items: [],
    grossTotal: 0,
    gstTotal: 0,
    roundOff: 0,
    discount: 0,
    cgst: 0,
    sgst: 0,
    igst: 0,
    cessAmount: 0,
    finalTotal: 0
  });

  const [suppliers, setSuppliers] = useState<Supplier[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  // Mock suppliers data (replace with actual API call)
  useEffect(() => {
    const mockSuppliers: Supplier[] = [
      { id: 1, name: 'PHARMACEUTICAL DISTRIBUTORS', address: '19/1132, P.V.SAMI ROAD, KOCHI', gstIn: '32ABCDE1234F1Z5' },
      { id: 2, name: 'MEDICAL SUPPLIES LTD', address: '45/678, MAIN STREET, ERNAKULAM', gstIn: '32FGHIJ5678K2L6' },
      { id: 3, name: 'HEALTH CARE DISTRIBUTORS', address: '12/345, CHURCH ROAD, KOCHI', gstIn: '32MNOPQ9012M3N7' }
    ];
    setSuppliers(mockSuppliers);
  }, []);

  // Fetch supplier details
  const fetchSupplierDetails = async () => {
    if (!purchaseBill.supplierId) return;

    setLoading(true);
    setError(null);

    try {
      // Mock API call - replace with actual API
      const selectedSupplier = suppliers.find(s => s.id === purchaseBill.supplierId);
      
      if (selectedSupplier) {
        setPurchaseBill(prev => ({
          ...prev,
          supplierName: selectedSupplier.name,
          supplierAddress: selectedSupplier.address,
          gstIn: selectedSupplier.gstIn
        }));
      }
    } catch (err) {
      setError('Failed to fetch supplier details');
    } finally {
      setLoading(false);
    }
  };

  // Add new item
  const addItem = () => {
    const newItem: PurchaseItem = {
      id: Date.now(),
      itemCode: '',
      medicineName: '',
      batchNo: '',
      expiryDate: '',
      qty: 0,
      freeQty: 0,
      purchaseRate: 0,
      sellingRate: 0,
      gstPercent: 0,
      amount: 0,
      hsnCode: ''
    };

    setPurchaseBill(prev => ({
      ...prev,
      items: [...prev.items, newItem]
    }));
  };

  // Remove item
  const removeItem = (itemId: number) => {
    setPurchaseBill(prev => ({
      ...prev,
      items: prev.items.filter(item => item.id !== itemId)
    }));
  };

  // Update item
  const updateItem = (itemId: number, field: keyof PurchaseItem, value: any) => {
    setPurchaseBill(prev => ({
      ...prev,
      items: prev.items.map(item => {
        if (item.id === itemId) {
          const updatedItem = { ...item, [field]: value };
          
          // Calculate amount when qty, purchaseRate, or gstPercent changes
          if (field === 'qty' || field === 'purchaseRate' || field === 'gstPercent') {
            const baseAmount = updatedItem.qty * updatedItem.purchaseRate;
            const gstAmount = (baseAmount * updatedItem.gstPercent) / 100;
            updatedItem.amount = baseAmount + gstAmount;
          }
          
          return updatedItem;
        }
        return item;
      })
    }));
  };

  // Calculate totals
  useEffect(() => {
    const grossTotal = purchaseBill.items.reduce((sum, item) => sum + (item.qty * item.purchaseRate), 0);
    const gstTotal = purchaseBill.items.reduce((sum, item) => sum + ((item.qty * item.purchaseRate * item.gstPercent) / 100), 0);
    const cgst = gstTotal / 2;
    const sgst = gstTotal / 2;
    const roundOff = Math.round((grossTotal + gstTotal - purchaseBill.discount) * 100) / 100 - (grossTotal + gstTotal - purchaseBill.discount);
    const finalTotal = grossTotal + gstTotal - purchaseBill.discount + roundOff + purchaseBill.cessAmount;

    setPurchaseBill(prev => ({
      ...prev,
      grossTotal,
      gstTotal,
      cgst,
      sgst,
      roundOff,
      finalTotal
    }));
  }, [purchaseBill.items, purchaseBill.discount, purchaseBill.cessAmount]);

  // Save purchase bill
  const savePurchaseBill = async () => {
    try {
      const purchaseData = {
        ...purchaseBill,
        items: purchaseBill.items
      };
      
      console.log('Saving purchase bill:', JSON.stringify(purchaseData, null, 2));
      
      // Mock API call - replace with actual save endpoint
      alert('Purchase bill saved successfully!');
    } catch (err) {
      setError('Failed to save purchase bill');
    }
  };

  // Delete purchase bill
  const deletePurchaseBill = () => {
    if (window.confirm('Are you sure you want to delete this purchase bill?')) {
      setPurchaseBill({
        purchaseId: purchaseBill.purchaseId + 1,
        date: new Date().toISOString().split('T')[0],
        supplierId: 0,
        supplierName: '',
        supplierAddress: '',
        gstIn: '',
        invoiceNo: '',
        invoiceDate: new Date().toISOString().split('T')[0],
        paymentType: 'Credit',
        items: [],
        grossTotal: 0,
        gstTotal: 0,
        roundOff: 0,
        discount: 0,
        cgst: 0,
        sgst: 0,
        igst: 0,
        cessAmount: 0,
        finalTotal: 0
      });
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 to-blue-50 p-4">
      <div className="max-w-7xl mx-auto">
        {/* Header */}
        <div className="bg-white rounded-lg shadow-lg p-6 mb-6">
          <div className="flex justify-between items-center mb-6">
            <h1 className="text-3xl font-bold text-gray-800">Purchase Bill</h1>
            <div className="flex space-x-3">
              <button
                onClick={savePurchaseBill}
                className="bg-green-600 hover:bg-green-700 text-white px-6 py-2 rounded-lg font-medium transition-colors duration-200 shadow-md"
              >
                Save
              </button>
              <button
                onClick={deletePurchaseBill}
                className="bg-red-600 hover:bg-red-700 text-white px-6 py-2 rounded-lg font-medium transition-colors duration-200 shadow-md"
              >
                Delete
              </button>
            </div>
          </div>

          {/* Form Fields */}
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {/* Purchase ID */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">Purchase ID</label>
              <input
                type="text"
                value={purchaseBill.purchaseId}
                readOnly
                className="w-full px-3 py-2 border border-gray-300 rounded-md bg-gray-50 text-gray-600 cursor-not-allowed"
              />
            </div>

            {/* Date */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">Date</label>
              <input
                type="date"
                value={purchaseBill.date}
                onChange={(e) => setPurchaseBill(prev => ({ ...prev, date: e.target.value }))}
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              />
            </div>

            {/* Supplier ID */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">Supplier ID</label>
              <div className="flex space-x-2">
                <select
                  value={purchaseBill.supplierId}
                  onChange={(e) => setPurchaseBill(prev => ({ ...prev, supplierId: Number(e.target.value) }))}
                  className="flex-1 px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                >
                  <option value={0}>Select Supplier</option>
                  {suppliers.map(supplier => (
                    <option key={supplier.id} value={supplier.id}>
                      {supplier.id} - {supplier.name}
                    </option>
                  ))}
                </select>
                <button
                  onClick={fetchSupplierDetails}
                  disabled={!purchaseBill.supplierId || loading}
                  className="bg-blue-600 hover:bg-blue-700 disabled:bg-gray-400 text-white px-4 py-2 rounded-md font-medium transition-colors duration-200"
                >
                  {loading ? 'Loading...' : 'GET'}
                </button>
              </div>
            </div>

            {/* Supplier Name */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">Supplier Name</label>
              <input
                type="text"
                value={purchaseBill.supplierName}
                readOnly
                className="w-full px-3 py-2 border border-gray-300 rounded-md bg-gray-50 text-gray-600"
              />
            </div>

            {/* Supplier Address */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">Supplier Address</label>
              <input
                type="text"
                value={purchaseBill.supplierAddress}
                readOnly
                className="w-full px-3 py-2 border border-gray-300 rounded-md bg-gray-50 text-gray-600"
              />
            </div>

            {/* GST IN */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">GST IN</label>
              <input
                type="text"
                value={purchaseBill.gstIn}
                readOnly
                className="w-full px-3 py-2 border border-gray-300 rounded-md bg-gray-50 text-gray-600"
              />
            </div>

            {/* Invoice No */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">Invoice No</label>
              <input
                type="text"
                value={purchaseBill.invoiceNo}
                onChange={(e) => setPurchaseBill(prev => ({ ...prev, invoiceNo: e.target.value }))}
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              />
            </div>

            {/* Invoice Date */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">Invoice Date</label>
              <input
                type="date"
                value={purchaseBill.invoiceDate}
                onChange={(e) => setPurchaseBill(prev => ({ ...prev, invoiceDate: e.target.value }))}
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              />
            </div>

            {/* Payment Type */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">Payment Type</label>
              <select
                value={purchaseBill.paymentType}
                onChange={(e) => setPurchaseBill(prev => ({ ...prev, paymentType: e.target.value }))}
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              >
                <option value="Credit">Credit</option>
                <option value="Cash">Cash</option>
                <option value="Other">Other</option>
              </select>
            </div>
          </div>

          {error && (
            <div className="mt-4 p-3 bg-red-100 border border-red-400 text-red-700 rounded-md">
              {error}
            </div>
          )}
        </div>

        {/* Items Section */}
        <div className="bg-white rounded-lg shadow-lg p-6 mb-6">
          <div className="flex justify-between items-center mb-4">
            <h2 className="text-xl font-semibold text-gray-800">Items</h2>
            <button
              onClick={addItem}
              className="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded-lg font-medium transition-colors duration-200 shadow-md flex items-center space-x-2"
            >
              <PlusIcon className="h-5 w-5" />
              <span>Add Item</span>
            </button>
          </div>

          <div className="overflow-x-auto">
            <table className="w-full border-collapse border border-gray-300">
              <thead>
                <tr className="bg-gradient-to-r from-blue-600 to-blue-700 text-white">
                  <th className="border border-gray-300 px-3 py-2 text-left">Item Code</th>
                  <th className="border border-gray-300 px-3 py-2 text-left">Medicine Name</th>
                  <th className="border border-gray-300 px-3 py-2 text-left">Batch No</th>
                  <th className="border border-gray-300 px-3 py-2 text-left">Expiry Date</th>
                  <th className="border border-gray-300 px-3 py-2 text-right">Qty</th>
                  <th className="border border-gray-300 px-3 py-2 text-right">Free Qty</th>
                  <th className="border border-gray-300 px-3 py-2 text-right">P.Rate</th>
                  <th className="border border-gray-300 px-3 py-2 text-right">S.Rate</th>
                  <th className="border border-gray-300 px-3 py-2 text-right">GST %</th>
                  <th className="border border-gray-300 px-3 py-2 text-right">Amount</th>
                  <th className="border border-gray-300 px-3 py-2 text-left">HSN Code</th>
                  <th className="border border-gray-300 px-3 py-2 text-center">Action</th>
                </tr>
              </thead>
              <tbody>
                {purchaseBill.items.map((item) => (
                  <tr key={item.id} className="hover:bg-gray-50">
                    <td className="border border-gray-300 px-2 py-1">
                      <input
                        type="text"
                        value={item.itemCode}
                        onChange={(e) => updateItem(item.id, 'itemCode', e.target.value)}
                        className="w-full px-2 py-1 border-none focus:ring-1 focus:ring-blue-500 rounded"
                      />
                    </td>
                    <td className="border border-gray-300 px-2 py-1">
                      <input
                        type="text"
                        value={item.medicineName}
                        onChange={(e) => updateItem(item.id, 'medicineName', e.target.value)}
                        className="w-full px-2 py-1 border-none focus:ring-1 focus:ring-blue-500 rounded"
                      />
                    </td>
                    <td className="border border-gray-300 px-2 py-1">
                      <input
                        type="text"
                        value={item.batchNo}
                        onChange={(e) => updateItem(item.id, 'batchNo', e.target.value)}
                        className="w-full px-2 py-1 border-none focus:ring-1 focus:ring-blue-500 rounded"
                      />
                    </td>
                    <td className="border border-gray-300 px-2 py-1">
                      <input
                        type="date"
                        value={item.expiryDate}
                        onChange={(e) => updateItem(item.id, 'expiryDate', e.target.value)}
                        className="w-full px-2 py-1 border-none focus:ring-1 focus:ring-blue-500 rounded"
                      />
                    </td>
                    <td className="border border-gray-300 px-2 py-1">
                      <input
                        type="number"
                        value={item.qty}
                        onChange={(e) => updateItem(item.id, 'qty', Number(e.target.value))}
                        className="w-full px-2 py-1 border-none focus:ring-1 focus:ring-blue-500 rounded text-right"
                      />
                    </td>
                    <td className="border border-gray-300 px-2 py-1">
                      <input
                        type="number"
                        value={item.freeQty}
                        onChange={(e) => updateItem(item.id, 'freeQty', Number(e.target.value))}
                        className="w-full px-2 py-1 border-none focus:ring-1 focus:ring-blue-500 rounded text-right"
                      />
                    </td>
                    <td className="border border-gray-300 px-2 py-1">
                      <input
                        type="number"
                        step="0.01"
                        value={item.purchaseRate}
                        onChange={(e) => updateItem(item.id, 'purchaseRate', Number(e.target.value))}
                        className="w-full px-2 py-1 border-none focus:ring-1 focus:ring-blue-500 rounded text-right"
                      />
                    </td>
                    <td className="border border-gray-300 px-2 py-1">
                      <input
                        type="number"
                        step="0.01"
                        value={item.sellingRate}
                        onChange={(e) => updateItem(item.id, 'sellingRate', Number(e.target.value))}
                        className="w-full px-2 py-1 border-none focus:ring-1 focus:ring-blue-500 rounded text-right"
                      />
                    </td>
                    <td className="border border-gray-300 px-2 py-1">
                      <input
                        type="number"
                        step="0.01"
                        value={item.gstPercent}
                        onChange={(e) => updateItem(item.id, 'gstPercent', Number(e.target.value))}
                        className="w-full px-2 py-1 border-none focus:ring-1 focus:ring-blue-500 rounded text-right"
                      />
                    </td>
                    <td className="border border-gray-300 px-2 py-1 text-right font-medium">
                      ₹{item.amount.toFixed(2)}
                    </td>
                    <td className="border border-gray-300 px-2 py-1">
                      <input
                        type="text"
                        value={item.hsnCode}
                        onChange={(e) => updateItem(item.id, 'hsnCode', e.target.value)}
                        className="w-full px-2 py-1 border-none focus:ring-1 focus:ring-blue-500 rounded"
                      />
                    </td>
                    <td className="border border-gray-300 px-2 py-1 text-center">
                      <button
                        onClick={() => removeItem(item.id)}
                        className="text-red-600 hover:text-red-800 transition-colors duration-200"
                      >
                        <TrashIcon className="h-5 w-5" />
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>

        {/* Summary Section */}
        <div className="bg-white rounded-lg shadow-lg p-6">
          <h2 className="text-xl font-semibold text-gray-800 mb-4">Summary</h2>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
            <div className="bg-gray-50 p-4 rounded-lg">
              <label className="block text-sm font-medium text-gray-700 mb-2">Gross Total</label>
              <div className="text-lg font-semibold text-gray-800">₹{purchaseBill.grossTotal.toFixed(2)}</div>
            </div>
            <div className="bg-gray-50 p-4 rounded-lg">
              <label className="block text-sm font-medium text-gray-700 mb-2">GST Total</label>
              <div className="text-lg font-semibold text-gray-800">₹{purchaseBill.gstTotal.toFixed(2)}</div>
            </div>
            <div className="bg-gray-50 p-4 rounded-lg">
              <label className="block text-sm font-medium text-gray-700 mb-2">CGST</label>
              <div className="text-lg font-semibold text-gray-800">₹{purchaseBill.cgst.toFixed(2)}</div>
            </div>
            <div className="bg-gray-50 p-4 rounded-lg">
              <label className="block text-sm font-medium text-gray-700 mb-2">SGST</label>
              <div className="text-lg font-semibold text-gray-800">₹{purchaseBill.sgst.toFixed(2)}</div>
            </div>
            <div className="bg-gray-50 p-4 rounded-lg">
              <label className="block text-sm font-medium text-gray-700 mb-2">Round Off</label>
              <div className="text-lg font-semibold text-gray-800">₹{purchaseBill.roundOff.toFixed(2)}</div>
            </div>
            <div className="bg-gray-50 p-4 rounded-lg">
              <label className="block text-sm font-medium text-gray-700 mb-2">Discount</label>
              <input
                type="number"
                step="0.01"
                value={purchaseBill.discount}
                onChange={(e) => setPurchaseBill(prev => ({ ...prev, discount: Number(e.target.value) }))}
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              />
            </div>
            <div className="bg-gray-50 p-4 rounded-lg">
              <label className="block text-sm font-medium text-gray-700 mb-2">Cess Amount</label>
              <input
                type="number"
                step="0.01"
                value={purchaseBill.cessAmount}
                onChange={(e) => setPurchaseBill(prev => ({ ...prev, cessAmount: Number(e.target.value) }))}
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              />
            </div>
            <div className="bg-red-50 p-4 rounded-lg border-2 border-red-200">
              <label className="block text-sm font-medium text-red-700 mb-2">Final Total</label>
              <div className="text-2xl font-bold text-red-600">₹{purchaseBill.finalTotal.toFixed(2)}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default PurchaseBillPage;